import titleimg from '../common/login/titileImg.vue'
import { mapGetters } from 'vuex'
export default {
  data(){
     return{
      msgT :false,
      imageSrc:'',
      nextdones:false,
      name:"张龙"
     }
  },
  components:{
      titleimg
    },
      computed:{
      ...mapGetters({
            username:'username'
          })
    },
   created(){
        document.title="创建成功"
        var img = getStore("userAll");
         this.imageSrc = JSON.parse(img).faceimg;
       this.name = this.username;
     // this.number = this.imageUrl[1]
  },
  methods:{
    nextStep(){
     this.nextdones = true;
    }
  }
}




